/* Movie.module.css */

.movie {
   background-color: white;
   margin-top: 50px;
   margin-bottom: 70px;
   margin-left: 50px;
   margin-right: 50px;
   font-weight: 300;
   padding: 20px;
   border-radius: 5px;
   color: aliceblue;
   display: grid;
   grid-template-columns: minmax(150px, 1fr) 2fr;
   grid-gap: 80px;
   text-decoration: none;
   color: inherit;
   box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie__img {
    position: relative;
    top: 10px;
    max-width: 300px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
      0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
  }


.movie__title,
.movie__year {
    margin: 0;
    font-weight: 300;
    text-decoration: none;
}
  
.movie__title a {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
    text-decoration: none;
}
  
.movie__genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 5px 0px;
}
  
.movie__genres li,
.movie__year {
    margin-right: 10px;
    font-size: 14px;
  }

.button {
    position: absolute;
    bottom: 20px;
    right: 20px;
    padding: 10px 20px;
    background-color: #2c2c2c;
    color: white;


}