/* Movie.module.css */

.thumbnail {
   background-color: white;
   margin-bottom: 70px;
   font-weight: 300;
   padding: 20px;
   padding-bottom: -50px;
   border-radius: 5px;
   color: aliceblue;
   display: grid;
   grid-template-columns: minmax(150px, 1fr) 2fr;
   grid-gap: 20px;
   text-decoration: none;
   color: inherit;
   box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    position: relative;
}

.thumbnail__img {
    position: relative;
    top: -50px;
    max-width: 150px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
      0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
  }


.thumbnail__title,
.thumbnail__year {
    margin: 0;
    font-weight: 300;
    text-decoration: none;
    gap: 100px;
}
  
.thumbnail__year {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
    text-decoration: none;
}
  
.thumbnail__genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 5px 0px;
    position: absolute;
    bottom: 60px;
    width: 100%;
}
  
.thumbnail__genres li,
.thumbnail__year {
    margin-right: 10px;
    font-size: 14px;
    white-space: nowrap;
  }